import { useState, useEffect } from 'react'
import Geocode from 'react-geocode'

// ç³è«çgoogle api key
import { apiKey } from '../../config/googleApi'

function GeocodeSearch(props) {
  const [address, setAddress] = useState('')

  // åélatèlngçç¶æ¯å±¤callbackå½å¼
  const { setLng, setLat } = props

  // useEffect(() => {}, [address])

  const handleGetGeocode = () => {
    // set Google Maps Geocoding API for purposes of quota management. Its optional but recommended.
    Geocode.setApiKey(apiKey)

    // set response language. Defaults to english.
    Geocode.setLanguage('zh-TW')

    // set response region. Its optional.
    // A Geocoding request with region=es (Spain) will return the Spanish city.
    Geocode.setRegion('tw')

    // Enable or disable logs. Its optional.
    Geocode.enableDebug()

    // Get latitude & longitude from address.
    Geocode.fromAddress(address).then(
      (response) => {
        const { lat, lng } = response.results[0].geometry.location
        console.log(lat, lng)
        setLat(lat)
        setLng(lng)
      },
      (error) => {
        console.error(error)
      }
    )
  }

  return (
    <>
      <input
        type="text"
        placeholder="è¼¸å¥ä½å"
        value={address}
        onChange={(e) => setAddress(e.target.value)}
      />
      <button onClick={handleGetGeocode}>é¡¯ç¤º</button>
    </>
  )
}

export default GeocodeSearch
