import React, { useEffect, useState } from 'react'

function ToTopButton(props) {
  // æéæ¯å¦åºç¾
  const [toTopBool, setToTopBool] = useState(false)

  // å°é 
  const handleScrollToTop = () => {
    console.log('click lemon')
    window.scrollTo({
      top: 0,
      left: 0,
      behavior: 'smooth',
    })
  }

  // åµæ¸¬è¦çªä½ç½®æ¹è®åºç¾ææ
  const handleBtnByScroll = () => {
    // console.log('i am scrolling')
    // console.log(window.pageYOffset)
    // window.pageYOffset > 500 && console.log(window.pageYOffset)
    if (window.pageYOffset > 200) {
      setToTopBool(true)
    }
    if (window.pageYOffset <= 200) {
      setToTopBool(false)
    }
  }

  // å¨è¼å¥éæ®µå æäºä»¶
  useEffect(() => {
    // console.log('didMount')
    window.addEventListener('scroll', handleBtnByScroll)
  }, [])

  // å¨å¸è¼éæ®µç§»é¤äºä»¶
  useEffect(() => {
    return () => {
      //   console.log('unMount')
      window.removeEventListener('scroll', handleBtnByScroll)
    }
  }, [])

  // å¦æå¨[è¼å¥éæ®µ]å·è¡'äºä»¶å æ', å»æ²æå¨[å¸è¼éæ®µ]å·è¡'äºä»¶ç§»é¤', å¨åä»¶åææ, æå°è´å æçäºä»¶æèç±[å¸è¼éæ®µ]éè¤è¢«å æå·è¡

  return (
    <>
      <div
        className="poe-toTop"
        style={toTopBool ? { right: '3%', opacity: '1' } : {}}
        onClick={() => {
          handleScrollToTop()
        }}
      >
        <i className="fas fa-chevron-up"></i>
      </div>
    </>
  )
}

export default ToTopButton
